/* Header styling */
.header {
  background-color: #001133;
  /* background-color: #00330e; */
  padding: 10px 20px;
  margin-bottom: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo-title-container {
  display: flex;
  align-items: center;
  margin-right: -140px;
} 

/* Desktop view: Logo container styles */
.logo-container {
  width: 150px;
  height: auto;
  margin-right: 15px;
  margin-top: -10px;
  padding-top: 0;  /* Resets padding for desktop */
  padding-right: 0; /* Resets padding for desktop */
}

.logo {
  margin-top: 10px;
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.header-title {
  font-size: 22px;
  color: #f4f0f0;
  font-weight: bold;
  text-align: center;
}

/* Mobile-specific styles */
@media screen and (max-width: 764px) {
  .logo-container {
    width: 100px;
    height: auto;
    padding-top: -20px;
    padding-right: 85px;
    margin-top: -10px;
    padding-left: -20px;
    
  }

  .header-title {
    font-size: 18px;
    text-align: center;
    margin-top: 30px;
    /* margin-right: 5px; */
    margin-left: -10px;
    padding-right: 30px;
    padding-left: 10px;
    
  }

  .nav_links {
    display: none;
    
  }

  .menu_icon, .mobile_search_icon {
    display: block;
    color: white;
    cursor: pointer;
    font-size: 25px;
  }

  .sidebar {
    background-color: #001133; /* Add your desired background color */
    width: 140px; /* Set the width of the sidebar */
    height: 70vh; /* Make it fill the full height of the viewport */
    position: fixed; /* Position it fixed for a classic sidebar effect */
    margin-top: 110px;
    padding: 20px;
    /* padding-top: 20px; */
    color: #f4f0f0; /* Text color for contrast */
    overflow-y: auto; /* Enable scrolling if the content is too tall */
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* Optional: add a shadow for depth */
  }

}

/* Desktop-specific styles */
@media screen and (min-width: 991px) {
  .header {
    flex-direction: row;
    align-items: center;
    padding: 10px 20px;
  }

  .logo-container {
    width: 150px;
    height: auto;
    margin-right: 15px;
    margin-top: 20px;
  }

  .logo {
    width: 150px; 
    height: 150px; 
    object-fit: contain;
  }

  .header-title {
    font-size: 22px;
    text-align: right;
  }

  .search_input {
    display: flex;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
  }

  .menu_icon, .mobile_search_icon {
    display: none !important;
  }

  .nav_links {
    display: flex;
    justify-content: flex-end;
    margin-left: auto;
  }
}
