/* General Styles */
html, body {
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

body {
  background-color: #001133;
}

/* Company Section Styles */
.company_section1 {
  background-color: #0f1e68ef;
  padding: 60px 20px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.company_ul1 {
  padding-left: 40px;
  color: #eff1f4;
  font-size: 25px;
  margin-right: 150px;
}

.about-button {
  background-color: #a50303;
  color: #eff1f4;
  font-size: 18px;
  padding: 12px 25px;
  margin-right: 70px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s ease, color 0.3s ease;
  white-space: nowrap;
}

.about-button:hover {
  background-color: #0056b3;
  color: #fff;
}

/* Responsive Design */
@media (max-width: 768px) {
  .company_section1 {
    flex-direction: column;
    text-align: center;
    padding: 30px 10px;
  }

  .company_ul1 {
    font-size: 13px;
    margin-right: 30px;
    margin-left: -10px;
    margin-top: -15px;
    text-align: center;
    margin-bottom: 10px; /* Adds space between text and button */
  }

  .about-button {
    font-size: 10px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-right: 10px;
    margin-bottom: -15px;
    
  }
}
